body {
  margin: 0;
  font-family: 'Libre Franklin', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Roboto Mono', 'monospace';
}

.App {
  text-align: center;
}

.App-logo {
  height: 30vmin;
}

.App-header {
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link a:link {
  transition: all 0.8s ease;
  background-color: #c1d6ed;
  color: white;
}

.App-link a:hover {
  color: white;
  background-color: #e1a3a3;
}

#footer {
  color: #000411;
  font-family: 'Libre Franklin', sans-serif;
  text-align: center;
}


.nav-item {
    padding-left: 10px;
    font-size: x-large;
    vertical-align: middle
}

.nav-link a:hover {
    text-decoration: none;
}

.navbar {
    background-color: #c1d6ed !important;
    font-family: 'Optima', serif !important; 
    font-weight: lighter;
    font-size:xx-large;
    color: #d14343;
    /* padding-bottom: 5px; */
    text-decoration: none;
    vertical-align: middle
}

.navbar a {
    color: black;
    vertical-align: middle
}

.navbar-brand {
    -webkit-text-decoration-color: #d14343;
            text-decoration-color: #d14343;
    color: #d14343;
    font-size: xx-large;
    vertical-align: middle
}

.navbar a:hover {
    text-decoration: none;
    color: #d14343;
}

.sticky {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 99;
    display: inline;
    /**
    font-family: 'Libre Franklin', sans-serif !important;
    font-size: x-large !important;
    color: pink !important;
    position: fixed !important;
    

     */
    vertical-align: middle
}

.links {
    display: flex;
    float: right;
    padding-right: 10px;
    vertical-align: middle;
    padding-top: 40px
}
.home {
    display: flex;
    flex-direction: column;
    justify-items: center;
}
.intro-box {
    display: flex;
    justify-content: center;
    padding-top: 23vh;
    align-items: center;
    margin-left: 1rem;
    margin-right: 1rem;
}

.logo {
    height: 500px;
    width: 500px;
}

.header-text {
    font-family: 'Optima', sans-serif;      
    line-height: 96.19%;
    max-width: 900px;
    min-width: 200px;
    font-size: 48px;
    font-weight: 200;
}

.links > a{
    color: #c1d6ed;
    display: inline-block;
    padding-right: 2rem;
}

.links > a:hover {
    color: #d14343;
}

.vl {
    display: flex;
    flex-direction: column;
    border-left: 1px solid #000411;
    margin-left: 2em;
    padding-left: 2em;
}

html {
    scroll-behavior: smooth;
}
/** media queries 576, 768, 992, 1200*/


@media screen and ( max-width: 1500px ) {
    .logo {
        height: 300px;
        width: 300px;
    }
    .header-text {
        font-size: 40px;
    }
    .intro-box {
        padding-top: 30vh;
    }
    .about-me {
        margin-bottom: 10vh;
    }
}

@media screen and ( max-width: 1300px ) {
    #about-photo {
        width: 540px;
        height: 405px;
    }
    .arrow {
        visibility: hidden;
    }
}

@media screen and ( max-width: 1000px ) {
    .intro-box {
        padding-top: 20vh;
    }
    .about-me {
        margin-top: 10vh;
        flex-wrap: wrap;
    }
    .arrow {
        margin-top: 5vh;
    }
    .about-content {
        margin-right: 0em;
    }
    #about-text {
        max-width: 540px;
    }
}

@media screen and ( max-width: 900px ) {
    .intro-box {
        flex-wrap: wrap;
    }
    .header-text {
        padding-top: 1rem;
    }
}

@media screen and ( max-width: 576px ) {
    #about-photo {
        width: 360px;
        height: 270px;
    }
    .header-text {
        font-size: 28px;
    }
}

